import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './assets/scss/index.scss'
import ElementPlus from 'element-plus'
import 'element-plus/dist/index.css'
import 'element-plus/theme-chalk/display.css'
import animated from 'animate.css'
import wallet from '@/plugins/wallet/index'
import './plugins/warpdrive/warpdrive.js'
import './assets/images/common/logo_32x32.png'
router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})
createApp(App).use(ElementPlus).use(store).use(router).use(animated).use(wallet).mount('#app')
